}

*,
*:before,
*:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}



a {
    text-decoration: none;
    cursor: pointer;
    color: inherit;
}

img {
    border: none;
    max-width: 100%;
}

a,
img {
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
    outline: none;
}
