.close {
  position: absolute;
  top: 10px;
  right: 10px;
  margin: 0 auto;
  width: 30px;
  height: 30px;
  display: block;
  text-align: center;
  cursor: pointer;
  font-size: 0;
  z-index: 50;
}

.close:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 2px;
  height: 100%;
  transform: rotate(45deg);
  transform-origin: center;
  -webkit-transform: rotate(45deg);
  -webkit-transform-origin: center;
  background: #000;
}

.close:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 2px;
  height: 100%;
  transform: rotate(-45deg);
  transform-origin: center;
  -webkit-transform: rotate(-45deg);
  -webkit-transform-origin: center;
  background: #000;
}
